import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Label, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../../models/dtos/ger/reportViewOptions';
import { CATEGORIA_ENUM_VALUES } from '../../../../../../models/enumerated/mfp/categoriaEnum';
import { MATRICULA, RELACAO_FUNCIONARIO_ORDEM_ENUM_VALUES } from '../../../../../../models/enumerated/mfp/relacaoFuncionarioOrdemEnum';
import { SIMPLES } from '../../../../../../models/enumerated/mfp/relacaoFuncionarioTipoEnum';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import ReportForm from '../../../../../components/report/ReportForm';
import { CLASS_KEY, RelacaoFuncionarioRV, REPORT_EVENT, REPORT_TITLE } from '../relacaoFuncionarioRV';

const DEFAULT_VALUES: RelacaoFuncionarioRV = {
  ativos: true,
  categoria: null,
  ...getDefaultReportBasicValues(`${REPORT_TITLE} Simplificada`),
  gerarCabecalhoPadraoAdministradora: true,
  ordem: MATRICULA.value,
  tipo: SIMPLES.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: true,
};

const defaultWidth: number = 100;

const RelacaoTrabalhadorSimplesReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<RelacaoFuncionarioRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const validateExtras = (parameter: RelacaoFuncionarioRV): string[] => {
    const errors = [];
    if (!parameter.ativos && !parameter.afastados && !parameter.demitidos) {
      errors.push('Situação - Campo não pode estar em branco.');
    }

    return errors;
  };

  return (
    <WindowComponent
      title="Relação de Trabalhadores"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={6578}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <input type="hidden" name="tipo" ref={formProps.register} />
        <Row>
          <Col>
            <InputGroupInline
              id="categoria"
              label="Categoria"
              labelWidth={defaultWidth}
              isPrependLabel={true}
              placeholder="Selecione a Categoria"
              formProps={{ ...formProps }}
              inputDropDownProps={{
                optionsProvider: CATEGORIA_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="ordem"
              label="Ordem"
              labelWidth={defaultWidth}
              isPrependLabel={true}
              placeholder="Selecione a Ordem"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: RELACAO_FUNCIONARIO_ORDEM_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <FormGroup tag={Row}>
          <Col>
            <Label style={{ marginRight: 20 }}>Situação</Label>
            <CustomInput id="checkAtivos" inline={true} label="Ativos" name="ativos" innerRef={formProps.register} type="switch" />
            <CustomInput id="checkAfastados" inline={true} label="Afastados" name="afastados" innerRef={formProps.register} type="switch" />
            <CustomInput id="checkDemitidos" inline={true} label="Demitidos" name="demitidos" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="checkImprimirSalario" label="Imprimir Salário" name="imprimirSalario" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>
      </ReportForm>
    </WindowComponent>
  );
};

export default RelacaoTrabalhadorSimplesReportView;
